@import 'assets/scss/variables.scss';

.container {
    padding: 0 1rem;
    display: flex;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    & .logo-wrapper {
        margin-bottom: 3.5rem;
        text-align: center;
        font-family: 'Roboto', sans-serif;
        font-size: 1.5rem;
        & .logo {
            height: 10rem;
            margin-bottom: 1rem;
        }
    }
}